/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState, createRef } from 'react'
import classNames from 'classnames'
import {
  CRow,
  CCol,
  CCard,
  CCardHeader,
  CCardBody,
  CBadge,
  CDataTable,
  CButton,
  CFormGroup,
  CLabel,
  CInput,
  CAlert,
  CSelect,
} from '@coreui/react'
import ModalForm from '../../../../components/ModalForm/ModalForm';
import ModalWarning from '../../../../components/ModalWarning/ModalWarning';
import {colors} from '../../coursesCategories/colors';

import {createTag, deleteTag, updateTag, getTags} from '../../../../services/tagService';
import {approve, disapprove, getInscriptions} from '../../../../services/inscriptionService';
import {useLocation} from 'react-router-dom';
import moment from 'moment';

const getBadge = highlight => {
  switch (highlight) {
    case 1: return 'primary'
    case 0: return 'danger'
    default: return 'primary'
  }
}

const fields = ['createdAt' ,{key: 'name', value: 'nombre', sorter: true}, 'email', 'completed' ,'actions']
const fields1 = {createdAt:'Fecha inscripción', name:'Nombre', completed:'¿Aprobado?', actions: 'Acciones'}
const columnsFilter = { description: 'Descripcion', financial_knowledge: 'conocimiento Financiero', value: 'valor', highlight: 'Destacado' }
// const fields2 = ['name']

const Students = ({history}) => {
  const location = useLocation();
  const [visibleForm, setVisibleForm] = useState(false);
  const [data, setData] = useState([]);
  const [deleteRow, setDeleteRow] = useState(false);
  const [isEditing, setEditing] = useState(false)
  const [error, setError] = useState(false)
  const [idForDelete, setIdForDelete] = useState(null);
  const [loading, setLoading] = useState(null);
  const [values, setValues] = useState({
    id: '',
    course: location.id,
    name: '',
  });
  const toggle = ()=>{
    setValues({
        id: '',
        course: location.id,
        name: '',
    })
    setVisibleForm(!visibleForm);
    setError(false);
    setEditing(false);
}
 useEffect (() => {
  handleLogout();
  getData();
},[])


const handleLogout = async () => {
    const token =  await localStorage.getItem('apitoken');
    if(token===null || token === undefined){
      history.push('/login');
    }
   }

 const getData = async () => {
   try {
    setLoading(true);
    const response = await getInscriptions(location.id);
    // let filter = response.data.map(data => (
    //   data.filter(alumno => alumno.course === location.id)
    // ))
    // console.log(filter);
    // let filtered = response.data.filter(res => res.course = location.id)
    await setData(response);
    if(response.length >= 0)setLoading(false);
   } catch(err){
     console.log(err)
   }
 }

 const storeTag = async () => {
    let tag = new FormData();
    if(values.name===''){
        setError(true)
    }else{
        tag.append('name', values.name);
        tag.append('course', values.course);
      try {
        const response = await createTag(tag)
        if(response) {
            console.log(response.data, 'tag creado');
            setVisibleForm(!visibleForm);
            await getData();
            setError(false)
        }
    } catch(err){
        console.log(err)
    }
}
 }

 const deleteItem = (id) => {
    setDeleteRow(!deleteRow);
    setIdForDelete(id)
  }

  const handleDelete = async (id) => {
     console.log(id,'si entra al delete')
     try {
    if(id!==null){
    await deleteTag(id)
  }
    } catch (err) {
        console.log(err);
    }
    setDeleteRow(!deleteRow);
    await getData();
  }

  const handleEdit = (item) => {

    setVisibleForm(!visibleForm);
    setEditing(true);
    setError(false);
    setValues({...values,
        id: item.id,
        name: item.name,
        email: item.email,
        completed: item.completed,
        course: item.course,
      })

  }

  const update = async () => {
    if(values.completed == 1){
          try {

            const response = await approve(values.id, location.id )
            if(response) {
                console.log(response.data, 'Aprobado');
                setVisibleForm(!visibleForm);
                await getData();
                setEditing(false);
            }
          } catch(err) {
              console.log(err);
          }
      } else {
        try {

          const response = await disapprove(values.id, location.id )
          if(response) {
              console.log(response.data, 'desprobado');
              setVisibleForm(!visibleForm);
              await getData();
              setEditing(false);
          }
        } catch(err) {
            console.log(err);
        }
      }
  }

  const handleChange = (e) => {
    e.preventDefault();
    setValues({...values,
      [e.target.name] : e.target.value
    })
    console.log(values);

  }

  return (
    <>
      <CCard>
        {/* <CCardHeader>
          <CButton
          color="primary"

      onClick={() => toggle()}
      className="mr-1"
    >Agregar Etiqueta</CButton>
        </CCardHeader> */}
        <CCol xs="12" lg="12">
          <CCard>
            <CCardHeader>
              Alumnos inscriptos en <b>{location.course.toUpperCase()}</b>
            </CCardHeader>
            <CCardBody>
            <CDataTable
              items={data}
              fields={fields}
              tableFilter={{label: 'Buscar', placeholder: 'Buscar Alumno'}}
              itemsPerPage={10}
              columnHeaderSlot={fields1}
              loading={loading}
              pagination
              striped
              responsive
              scopedSlots = {{
                'name':
            (item) => (
            <td style={{maxHeight:20, height: 20, maxWidth: 80}}>{`${item.name}`}</td>
            ),
            'createdAt':
            (item) => (
            <td style={{maxHeight:20, height: 20, maxWidth: 80}}>{`${moment.unix(item.createdAt / 1000).format('l')}`}</td>
            ),
            'completed':
                  (item)=>(
                    <td style={{maxHeight:20, height: 20, maxWidth: 80}}>
                      <CBadge color={getBadge(item.completed)}>
                        {item.completed == 1 ? 'Si' : 'No'}
                      </CBadge>
                    </td>
                  ),
                'actions':
            (item) => (
              <td>
                <CButton style={{marginRight: 10}} onClick={() => handleEdit(item)} color="warning">Aprobar/Desaprobar</CButton>
                {/* <CButton onClick={() => deleteItem(item.id)} color="danger">Eliminar</CButton> */}
              </td>
            ),

            }
          }
            />
            </CCardBody>
          </CCard>
        </CCol>
      </CCard>
      <ModalForm visible={visibleForm} modalToggle={toggle} onSubmit={!isEditing ? () => storeTag() : () => update()} title={`Curso ${location.course.toUpperCase()}`} >
      <CCol xs="12" sm="12">
          <CCard>
            <CCardBody>
              <CFormGroup>
                <CLabel htmlFor="name">Nombre</CLabel>
                <CInput id="name" disabled name="name" value={values.name} onChange={(e) => handleChange(e)} placeholder="Alumno" />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="email">Email</CLabel>
                <CInput id="email" disabled name="email" value={values.email} onChange={(e) => handleChange(e)} placeholder="Email" />
              </CFormGroup>
              <CFormGroup row>
              <CCol md="3">
                    <CLabel> ¿Aprobado?</CLabel>
                  </CCol>
                  <CCol md="9">
                  <CFormGroup variant="custom-checkbox" inline>
                <CSelect onChange={(e) => handleChange(e)} value={values.completed} custom name="completed" id="completed">
                       <>
                       <option value={1}>Si</option>
                       <option value={0}>No</option>
                        </>
                    </CSelect>
                    </CFormGroup>
                    </CCol>
              </CFormGroup>
              { error ? <CCol>
                    <CFormGroup>
                        <CAlert color="danger">
                         Debes ingresar todos los campos!
                        </CAlert>
                    </CFormGroup>
                </CCol>: null}
                {/* <CFormGroup row>
                  <CLabel col md="3" htmlFor="image">Imagen</CLabel>
                  <CCol xs="12" md="9">
                    <CInputFile id="image" name="image"/>
                  </CCol>
                </CFormGroup> */}
            </CCardBody>
          </CCard>
        </CCol>
      </ModalForm>
      <ModalWarning visible={deleteRow} onSubmit={() => handleDelete(idForDelete)} modalToggle={() => deleteItem(null)} title="Eliminar Etiqueta">Estas seguro de eliminar esta Etiqueta?</ModalWarning>

    </>
  )
}

export default Students
